.card__like {
  align-self: center;
  width: 22px;
  height: 19px;
  padding: 0;
  background-color: transparent;
  background-image: url(../../../images/elements/like-disabled.svg);
  border: none;
  transition: opacity .3s ease;
  cursor: pointer;
}

.card__like:hover {
  opacity: .5;
}